import React from 'react';
import Home from './Home';
import web from '../src/images/insta.png'
import home from '../src/images/home.png';
import bell from '../src/images/bell.png';
import heart from '../src/images/heart.png';
import message from '../src/images/message.png';
import Card from './Card';
import Sdata from './Sdata';
import {NavLink} from 'react-router-dom';
import Like from '../src/images/like.png';

const Navbar=()=>{

  
    return(
        <>

<div className='container-fluid'>
<div className='mx-auto'>

<div className='row'>



<nav className="navbar navbar-expand-lg navbar-light bg-white border">
  <div className="container-fluid">
  <NavLink className='navbar_brand1' to='/' >
    <img src={web} alt='insta' ></img>
    </NavLink>
  <div className='col-7 mx-auto'>
 
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">

      <div className='nav_search'>
      <form className="d-flex center">
        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"  />
      </form>
      </div>
      <div className='nav_left'>
      <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
      
        <li className="nav-item">
        <NavLink className='navbar_brand' to='/home' >
        <img src={home} alt='home' width="25px" height="25px" ></img>
        </NavLink>
        </li>
        <li className="nav-item">
        <NavLink className='navbar_brand' to='/message' >
        <img src={message} alt='home' width="26px" height="26px" ></img>
        </NavLink>
        </li>
        <li className="nav-item">
        <NavLink className='navbar_brand' to='/about' >
        <img src={bell} alt='bell' width="25px" height="25px"></img>
        </NavLink>
        </li>
        <li className="nav-item">
        <NavLink className='navbar_brand' to='/like' >
        <img src={Like} alt='bell' width="25px" height="25px"></img>
        </NavLink>
        </li>
            
        <li className="nav-item dropdown">
          <NavLink className="nav-link dropdown-toggle" to="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
          <img src={heart} alt='heart ' width="25px" height="25px" ></img>
          </NavLink>
          <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
            <li><a className="dropdown-item" to="#">Action</a></li>
            <li><a className="dropdown-item" to="#">Another action</a></li>
            <li><hr className="dropdown-divider"/></li>
            <li><a className="dropdown-item" to="#">Something else here</a></li>
          </ul>
        </li> 

        </ul>
      </div>




    </div>
  </div>
  </div>
</nav>


</div>
</div>
</div>



        </>


    )

};
export default Navbar;
