import React from 'react';
import './Post.css';
import football from '../src/images/football.png';
import heart1 from '../src/images/heart1.png';
import comment from '../src/images/comment.png';
import share from '../src/images/share.png';

function Post({username,caption,imageUrl}){
    
    return(
        <>
        <div className='container-fluid bg-light'>
<div className='col-7 mx-auto'>
<div className='row'>


<div className='post1'>

<div className='post'>
<img className='user_img' src={football} alt='football' width="30px" height="30px"></img>
        <h3>{username}</h3>
        <h3 style={{marginLeft: 'auto', marginRight:'20px'}}><strong>...</strong></h3>
        </div>
        <img className='post_image' src={imageUrl} alt='pic' ></img>
        
        
        <div className='caption'>
        <img  src={heart1} width="40px" height="40px" style={{padding:'10px' }}></img>
        <img  src={comment} width="40px" height="40px"></img>
        <img  src={share} width="40px" height="40px"></img>
        </div>
        <h3 className='font'>Liked by <strong>{username}</strong> and {caption}</h3>
        <textarea className='textarea1' cols='30' rows='1' placeholder="Add a comment.."></textarea>
        </div>
       
        </div>
        </div>
        </div>
        
        </>
    )
};
export default Post;